@import "../partials.scss";

.about {
  margin: 0 auto!important;
  padding: 6rem 0 3rem!important;
  text-align: center;
  background-color: #fff;
  color: darken($primary-color, 10%);

  h3 {
    margin-bottom: 3.5rem;
    font-size: 2rem;
    font-weight: normal;

    @media screen and (min-width: 768px) {
      font-size: 2.4rem;
    }
    
    @media screen and (min-width: 992px) {
      font-size: 3rem;
    }

    &::after {
      content: '';
      display: block;
      width: 5rem;
      height: 0.15rem;
      margin: 1rem auto;
      background-color: $candy-apple-red;
    }
  }

  .about-item {
    flex: 0 0 100%;
    margin-bottom: 2rem;
    padding: 3rem;
    border: 1px solid rgba(0, 0, 0, 0.05);
    border-radius: 3rem 3rem 3rem 0;

    &.first {
      transition: box-shadow 0.5s ease, opacity 1s ease, transform 1s ease;
    }
    &.second {
      transition: box-shadow 0.5s ease, opacity 1s ease 0.5s, transform 1s ease 0.5s;
    }
    &.third {
      transition: box-shadow 0.5s ease, opacity 1s ease 1s, transform 1s ease 1s;
    }

    @media screen and (min-width: 768px) {
      opacity: 0;
      transform: translateY(30px);

      &.lazy-loaded.first {
        opacity: 1;
        transform: translateY(0);
      }

      &.lazy-loaded.second {
        opacity: 1;
        transform: translateY(0);
      }

      &.lazy-loaded.third {
        opacity: 1;
        transform: translateY(0);
      }
    }
    
    @media screen and (min-width: 768px) {
      flex: 0 0 30%;
      padding: 3rem 0.5rem;
    }

    @media screen and (min-width: 992px) {
      flex: 0 0 32%;
      padding: 3rem;
    }

    &:hover {
      box-shadow: 1px 1px 30px 3px rgba(0, 0, 0, 0.05);
    }

    [class^="ti-"] {
      font-size: 2.2rem;
      color: $candy-apple-red;
    }

    h4 {
      margin: 1.5rem 0 1rem 0;
      font-size: 1.4rem;
      font-weight: normal;
    }

    p {
      line-height: 1.5;
      color: rgba(0,0,0,0.7);
    }
  }
}